import React from 'react';
import { connect } from 'react-redux';
import { Row } from 'reactstrap';
import Breadcrumb from '../../../../containers/navs/Breadcrumb';
import { Colxx } from '../../../../components/common/CustomBootstrap';
import EditProfileActions from './EditProfileActions';
import UserCard from './UserCard';
import useFetch from '../../../../hooks/use-fetch';
import { getCurrentUser, getProfileData } from '../../../../helpers/Utils';
import AppError from '../../../../helpers/AppError';
import HackCard from '../../../../containers/application/HackathonCard';

const ProfileView = ({ match, currentUser}) => {
  const { id } = getCurrentUser();
  const profileData = getProfileData();
  const { loading, data, error } = useFetch(`/getHackathonList/${id}`);


  if (loading) return <div className="loading" />;

  if (error) throw new AppError(error);

  return (
    <>
      <Row>
        <Colxx xxs="12">
          <Row>
            <Colxx className="mb-0">
              <h1>{profileData ? profileData.organization_name : "NO"}</h1>
              <EditProfileActions />
            </Colxx>
          </Row>

          <Breadcrumb match={match} />

          <Row>
            <Colxx xxs="12" lg="12" className="mb-4 col-left">
              <UserCard currentUser={currentUser} />
            </Colxx>
            <Colxx xxs="12" lg="7" className="mb-4 col-right">
              <Row>
              {data.map((item) => {
                if(item.name !== undefined){
                return <Colxx xxs="12" sm="6" md="6" key={item.id} >
                  <HackCard data={item} showEdit={item.submit_status === 0} />
                </Colxx>
                }
                return null;
              })}
                
              </Row>
            </Colxx>
          </Row>
        </Colxx>
      </Row>
    </>
  );
};

const mapStateToProps = ({ authUser }) => {
  const { currentUser, profileData } = authUser;
  return { currentUser, profileData };
};

const mapActionsToProps = {};

export default connect(mapStateToProps, mapActionsToProps)(ProfileView);
