import React from 'react';
import { Row } from 'reactstrap';
import {
  Colxx,
  Separator,
} from '../../../../components/common/CustomBootstrap';
import Breadcrumb from '../../../../containers/navs/Breadcrumb';

const FeedBackView = ({ match }) => {
  return (
    <>
      <Row>
        <Colxx xxs="12">
          <Breadcrumb heading="menu.feedback" match={match} />
          <Separator className="mb-5" />
        </Colxx>
      </Row>
      {/* <Row>
        <Colxx xxs="12" className="mb-4">
          <p>
            <IntlMessages id="menu.feedback" />
          </p>
        </Colxx>
      </Row> */}
      <Row>
          <Colxx xxs="12">
            <div className="alert alert-primary text-center mb-5 mx-5">
              You have<br /><span className="display-1"><b>0</b></span><br />Feedbacks Yet!
            </div>
          </Colxx>
        </Row>
    </>
  );
};

export default FeedBackView;
