import React, { useState } from 'react';
import { NavLink } from 'react-router-dom';
import {
  Card,
  Button,
  CardBody,
  InputGroupAddon,
  CustomInput,
  ModalBody,
  InputGroup,
  ModalHeader,
  Modal,
} from 'reactstrap';
import SingleLightbox from '../../../../components/pages/SingleLightbox';
import instance from '../../../../helpers/axiosInstance';
import IntlMessages from '../../../../helpers/IntlMessages';
import { getProfileData, getProfileDataAsync } from '../../../../helpers/Utils';

const UserCard = () => {
  const profileData = getProfileData();
  const id = profileData?.user_id;
  const id2 = profileData?.id;
  const [values, setValues] = useState({
    id,
    id2,
    organization_logo: null
  });


  const [openUpload, setOpenUpload] = useState(false);
  const ins = "https://";
  const formData = new FormData();
  console.log(profileData);
  const handleFileChange = (event) => {
    setValues({ ...values, [event.target.id]: event.target.files[0] });
  };
  
  const handleUpload = async (event) => {
    formData.append('user_id', profileData.user_id);
    formData.append('id', profileData.id);
    formData.delete(event.target.id);

    formData.append(`${event.target.id}`, values[event.target.id]);
    console.log(formData);
    await instance({
      url: '/updateProfile',
      method: 'POST',
      headers: {
        'Content-Type': 'multipart/form-data',
      },
      data: formData,
      timeout: 100000,
    });

    await getProfileDataAsync(profileData.user_id);
    setOpenUpload(false);
  };

 return (
    <Card className="mb-4">
      <div className="position-absolute card-top-buttons">
        <Button
          outline
          color="primary"
          className="icon-button"
          onClick={() => setOpenUpload(true)}
        >
          <i className="simple-icon-pencil" />
        </Button>
      </div>
      {profileData?.organization_logo && (
        
        <SingleLightbox
          thumb={profileData?.organization_logo}
          large={profileData?.organization_logo}
          className="card-img-special"
        />
      )}

      <CardBody>
        <p className="text-muted text-small">
          <IntlMessages id="menu.about" />
        </p>

        <p className="mb-3">
          {
            profileData?.bio
          }
        </p>

        <p className="text-muted text-small mb-2">
          <IntlMessages id="pages.location" />
        </p>

        <p className="mb-3">{profileData?.country}</p>


        <p className="text-muted text-small mb-2">
          <IntlMessages id="menu.contact" />
        </p>

        <div className="social-icons">
          <ul className="list-unstyled list-inline">
            <li className="list-inline-item">
              <NavLink to={{ pathname: ins + profileData?.linkedin }} target="_blank" location={{}}>
              
                <i className="simple-icon-social-linkedin" />
              </NavLink>
            </li>
            <li className="list-inline-item">
              <NavLink to="#" location={{}}>
                <i className="simple-icon-social-website-url" />
              </NavLink>
            </li>
            <li className="list-inline-item">
              <NavLink to={{ pathname: ins + profileData?.instagram }}  target="_blank" location={{}}>
                <i className="simple-icon-social-instagram" />
              </NavLink>
            </li>
          </ul>
        </div>
      </CardBody>
      <Modal isOpen={openUpload} toggle={() => setOpenUpload(!openUpload)}>
        <ModalHeader>
          <IntlMessages id="upload organization logo" />
        </ModalHeader>

        <ModalBody>
          <InputGroup>
            <CustomInput
              type="file"
              id="organization_logo"
              name="customFile"
              accept="image/jpeg, image/jpg, image/png"
              onChange={handleFileChange}
            />
            <InputGroupAddon addonType="append">
              <Button
                outline
                color="secondary"
                id="organization_logo"
                onClick={handleUpload}
              >
                <IntlMessages id="challenges.upload-button" />
              </Button>
            </InputGroupAddon>
          </InputGroup>
        </ModalBody>
      </Modal>
    </Card>
  );
};

export default UserCard;
